  align-self: flex-start;\n}\n\n.c-section-banner__image {\n  position: relative;\n  height: auto;\n  display: block;\n  align-self: center;\n  justify-content: flex-end;\n}\n\n@media (max-width: 1200px) {\n  .c-section-banner__image {\n    transform: scale(0.8) !important;\n  }\n}\n\n@media (min-width: 851px) {\n  .c-section-banner__image {\n    left: calc(50% + 80px);\n    position: absolute;\n    top: 0;\n  }\n}\n\n@media (min-width: 1201px) {\n  .c-section-banner__image {\n    transform: scale(1);\n    right: -160px;\n    left: auto;\n  }\n}\n\n.c-section-banner__image picture {\n  border-radius: 50%;\n  overflow: hidden;\n  z-index: 1;\n  position: relative;\n  border: 5vw solid #f33f4b;\n  width: 75%;\n  height: 75%;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n@media (min-width: 851px) {\n  .c-section-banner__image picture {\n    width: 600px;\n    height: 600px;\n    min-width: 600px;\n    margin-left: 0;\n    border-width: 40px;\n  }\n}\n\n.c-section-banner__image::before {\n  content: \"\";\n  display: none;\n  background-color: #f33f4b;\n  height: 100%;\n  width: 50vw;\n  position: absolute;\n  left: 50%;\n  top: 0;\n  z-index: 0;\n}\n\n@media (min-width: 851px) {\n  .c-section-banner__image::before {\n    display: none;\n  }\n}\n\n.c-section-banner__image::after {\n  content: \"\";\n  display: block;\n  height: 100%;\n  width: 100%;\n  position: absolute;\n  z-index: 0;\n  border-left: 50vw solid transparent;\n  border-right: 50vw solid transparent;\n  border-top: 60vw solid #f33f4b;\n  transform: translate(-50%, 0);\n  left: 50%;\n  top: 50%;\n}\n\n@media (min-width: 851px) {\n  .c-section-banner__image::after {\n    border-top: 400px solid transparent;\n    border-bottom: 400px solid transparent;\n    border-right: 460px solid #f33f4b;\n    border-left: none;\n    transform: translate(0, -50%);\n    left: -50%;\n    top: 50%;\n  }\n}\n\n/**\n * Cards\n */\n\n.c-section-cards__buttons {\n  display: flex;\n  align-items: center;\n  justify-content: