th d=\"M233.67,66.67c36.67,0,66.33,30,66.33,66.67a66.67,66.67,0,1,1-133.32,2.07c0-.52,0-1,0-1.55v-.52A133.3,133.3,0,0,1,299.93,0H300S256.33,16.33,233.67,66.67ZM133.33,133.33A66.67,66.67,0,1,1,0,135.4c0-.52,0-1,0-1.55v-.52H0A133.31,133.31,0,0,1,133.27,0h.07S89.67,16.33,67,66.67C103.67,66.67,133.33,96.67,133.33,133.33Z\" fill=\"#{$color}\"/></svg>');\n}\n","/* ------------------------------------ *\\\n    $RESET\n\\* ------------------------------------ */\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n  font-family: $ff-font;\n  font-size: $font-size-body;\n  background: $c-gray--lightest;\n  color: $c-body-color;\n}\n\nblockquote,\nbody,\ndiv,\nfigure,\nfooter,\nform,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\nheader,\nhtml,\niframe,\nlabel,\nlegend,\nli,\nnav,\nobject,\nol,\np,\nsection,\ntable,\nul {\n  margin: 0;\n  padding: 0;\n}\n\narticle,\nfigure,\nfooter,\nheader,\nhgroup,\nnav,\nsection {\n  display: block;\n}\n\naddress {\n  font-style: normal;\n}\n\nimg,\nvideo,\nobject,\nsvg,\niframe {\n  max-width: 100%;\n  border: none;\n  display: block;\n}\n\na {\n  text-decoration: none;\n  transition: $transition-all;\n  color: inherit;\n\n  svg path {\n    fill: currentColor;\n  }\n}\n","/* ------------------------------------ *\\\n    $HEADINGS\n\\* ------------------------------------ */\n\n@mixin o-heading--xxl {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xxl;\n  font-style: normal;\n  font-weight: 800;\n  text-transform: normal;\n  line-height: 1.2;\n  letter-spacing: normal;\n}\n\nh1,\n.o-heading--xxl {\n  @include o-heading--xxl;\n}\n\n@mixin o-heading--xl {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xl;\n  font-style: normal;\n  font-weight: 800;\n  text-transform: normal;\n  line-height: 1.2;\n  letter-spacing: normal;\n}\n\nh2,\n.o-heading--xl {\n  @include o-heading--xl;\n}\n\n@mixin o-heading--l {\n  font-family: $ff-font--primary;\n  font-size: $font-size-l;\n  font-style: normal;\n  font-weight: 600;\n  text-transform: inherit;\n  line-height: 1.4;\n  letter-spacing: normal;\n}\n\nh3,\n.o-heading--l {\n  @include o-heading--l;\n}\n\n@mixin o-heading--m {\n  font-family: $ff-font--primary;\n  font-size: $font-size-m;\n  font-style: normal;\n  font-weight: 600;\n  line-height: 1.6;\n  text-transform: uppercase;\n  letter-spacing: 1px;\n}\n\nh4,\n.o-heading--m {\n  @include o-heading--m;\n}\n\n@mixin o-heading--s {\n  font-family: $ff-font--primary;\n  font-size: $font-size-s;\n  font-style: normal;\n  font-weight: 500;\n  line-height: 1.6;\n  text-transform: uppercase;\n  letter-spacing: 1px;\n}\n\nh5,\n.o-heading--s {\n  @include o-heading--s;\n}\n\n@mixin o-heading--xs {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xs;\n  font-style: normal;\n  font-weight: bold;\n  line-height: 1.6;\n  text-transform: uppercase;\n  letter-spacing: 1.2px;\n}\n\nh6,\n.o-heading--xs {\n  @include o-heading--xs;\n}\n","/* ------------------------------------*\\\n    $SPACING\n\\*------------------------------------ */\n\n$sizes: (\"\": $space, --quarter: $space / 4, --half: $space / 2, --and-half: $space * 1.5, --double: $space * 2, --triple: $space * 3, --quad: $space * 4, --zero: 0);\n\n$sides: (\"\": \"\", --top: \"-top\", --bottom: \"-bottom\", --left: \"-left\", --right: \"-right\");\n\n@each $size_key, $size_value in $sizes {\n  .u-spacing#{$size_key} {\n    & > * + * {\n      margin-top: #{$size_value};\n    }\n  }\n\n  @each $side_key, $side_value in $sides {\n    .u-padding#{$size_key}#{$side_key} {\n      padding#{$side_value}: #{$size_value};\n    }\n\n    .u-space#{$size_key}#{$side_key} {\n      margin#{$side_value}: #{$size_value};\n    }\n  }\n}\n\n.u-spacing--left {\n  & > * + * {\n    margin-left: $space;\n  }\n}\n","/* ------------------------------------ *\\\n    $LAYOUT\n\\* ------------------------------------ */\n\nbody {\n  position: relative;\n\n  &::before {\n    content: \"\";\n    display: block;\n    height: 100vh;\n    width: 100vw;\n    background-color: $c-overlay;\n    position: fixed;\n    top: 0;\n    left: 0;\n    transition: all 0.5s ease;\n    transition-delay: 0.25s;\n    opacity: 0;\n    visibility: hidden;\n    z-index: 0;\n  }\n}\n\n/**\n * Wrapping element to keep content contained and centered.\n */\n@mixin l-wrap {\n  position: relative;\n  width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n  padding-left: $space;\n  padding-right: $space;\n\n  @include media(\">xlarge\") {\n    padding-left: $space-double;\n    padding-right: $space-double;\n  }\n}\n\n.l-wrap {\n  @include l-wrap;\n}\n\n/**\n * Layout containers - keep content centered and within a maximum width. Also\n * adjusts left and right padding as the viewport widens.\n */\n\n@mixin l-container {\n  position: relative;\n  width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n  max-width: $max-width;\n}\n\n.l-container {\n  @include l-container;\n\n  &--s {\n    width: 100%;\n    max-width: $small;\n  }\n\n  &--m {\n    width: 100%;\n    max-width: $medium;\n  }\n\n  &--l {\n    width: 100%;\n    max-width: $large;\n  }\n\n  &--xl {\n    width: 100%;\n    max-width: $max-width-xl;\n  }\n}\n","@charset \"UTF-8\";\n\n//     _            _           _                           _ _\n//    (_)          | |         | |                         | (_)\n//     _ _ __   ___| |_   _  __| | ___   _ __ ___   ___  __| |_  __ _\n//    | | '_ \\ / __| | | | |/ _` |/ _ \\ | '_ ` _ \\ / _ \\/ _` | |/ _` |\n//    | | | | | (__| | |_| | (_| |  __/ | | | | | |  __/ (_| | | (_| |\n//    |_|_| |_|\\___|_|\\__,_|\\__,_|\\___| |_| |_| |_|\\___|\\__,_|_|\\__,_|\n//\n//      Simple, elegant and maintainable media queries in Sass\n//                        v1.4.9\n//\n//                http://include-media.com\n//\n//         Authors: Eduardo Boucas (@eduardoboucas)\n//                  Hugo Giraudel (@hugogiraudel)\n//\n//      This project is licensed under the terms of the MIT license\n\n////\n/// include-media library public configuration\n/// @author Eduardo Boucas\n/// @access public\n////\n\n///\n/// Creates a list of global breakpoints\n///\n/// @example scss - Creates a single breakpoint with the label `phone`\n///  $breakpoints: ('phone': 320px);\n///\n$breakpoints: (\n  'phone': 320px,\n  'tablet': 768px,\n  'desktop': 1024px\n) !default;\n\n///\n/// Creates a list of static expressions or media types\n///\n/// @example scss - Creates a single media type (screen)\n///  $media-expressions: ('screen': 'screen');\n///\n/// @example scss - Creates a static expression with logical disjunction (OR operator)\n///  $media-expressions: (\n///    'retina2x': '(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)'\n///  );\n///\n$media-expressions: (\n  'screen': 'screen',\n  'print': 'print',\n  'handheld': 'handheld',\n  'landscape': '(orientation: landscape)',\n  'portrait': '(orientation: portrait)',\n  'retina2x': '(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx)',\n  'retina3x': '(-webkit-min-device-pixel-ratio: 3), (min-resolution: 350dpi), (min-resolution: 3dppx)'\n) !default;\n\n///\n/// Defines a number to be added or subtracted from each unit when declaring breakpoints with exclusive intervals\n///\n/// @example scss - Interval for pixels is defined as `1` by default\n///  @include media('>128px') {}\n///\n///  /* Generates: */\n///  @media(min-width: 129px) {}\n///\n/// @example scss - Interval for ems is defined as `0.01` by default\n///  @include media('>20em') {}\n///\n///  /* Generates: */\n///  @media(min-width: 20.01em) {}\n///\n/// @example scss - Interval for rems is defined as `0.1` by default, to be used with `font-size: 62.5%;`\n///  @include media('>2.0rem') {}\n///\n///  /* Generates: */\n///  @media(min-width: 2.1rem) {}\n///\n$unit-intervals: (\n  'px': 1,\n  'em': 0.01,\n  'rem': 0.1,\n  '': 0\n) !default;\n\n///\n/// Defines whether support for media queries is available, useful for creating separate stylesheets\n/// for browsers that don't support media queries.\n///\n/// @example scss - Disables support for media queries\n///  $im-media-support: false;\n///  @include media('>=tablet') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///  /* Generates: */\n///  .foo {\n///    color: tomato;\n///  }\n///\n$im-media-support: true !default;\n\n///\n/// Selects which breakpoint to emulate when support for media queries is disabled. Media queries that start at or\n/// intercept the breakpoint will be displayed, any others will be ignored.\n///\n/// @example scss - This media query will show because it intercepts the static breakpoint\n///  $im-media-support: false;\n///  $im-no-media-breakpoint: 'desktop';\n///  @include media('>=tablet') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///  /* Generates: */\n///  .foo {\n///    color: tomato;\n///  }\n///\n/// @example scss - This media query will NOT show because it does not intercept the desktop breakpoint\n///  $im-media-support: false;\n///  $im-no-media-breakpoint: 'tablet';\n///  @include media('>=desktop') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///  /* No output */\n///\n$im-no-media-breakpoint: 'desktop' !default;\n\n///\n/// Selects which media expressions are allowed in an expression for it to be used when media queries\n/// are not supported.\n///\n/// @example scss - This media query will show because it intercepts the static breakpoint and contains only accepted media expressions\n///  $im-media-support: false;\n///  $im-no-media-breakpoint: 'desktop';\n///  $im-no-media-expressions: ('screen');\n///  @include media('>=tablet', 'screen') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///   /* Generates: */\n///   .foo {\n///     color: tomato;\n///   }\n///\n/// @example scss - This media query will NOT show because it intercepts the static breakpoint but contains a media expression that is not accepted\n///  $im-media-support: false;\n///  $im-no-media-breakpoint: 'desktop';\n///  $im-no-media-expressions: ('screen');\n///  @include media('>=tablet', 'retina2x') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///  /* No output */\n///\n$im-no-media-expressions: ('screen', 'portrait', 'landscape') !default;\n\n////\n/// Cross-engine logging engine\n/// @author Hugo Giraudel\n/// @access private\n////\n\n\n///\n/// Log a message either with `@error` if supported\n/// else with `@warn`, using `feature-exists('at-error')`\n/// to detect support.\n///\n/// @param {String} $message - Message to log\n///\n@function im-log($message) {\n  @if feature-exists('at-error') {\n    @error $message;\n  }\n\n  @else {\n    @warn $message;\n    $_: noop();\n  }\n\n  @return $message;\n}\n\n///\n/// Determines whether a list of conditions is intercepted by the static breakpoint.\n///\n/// @param {Arglist}   $conditions  - Media query conditions\n///\n/// @return {Boolean} - Returns true if the conditions are intercepted by the static breakpoint\n///\n@function im-intercepts-static-breakpoint($conditions...) {\n  $no-media-breakpoint-value: map-get($breakpoints, $im-no-media-breakpoint);\n\n  @each $condition in $conditions {\n    @if not map-has-key($media-expressions, $condition) {\n      $operator: get-expression-operator($condition);\n      $prefix: get-expression-prefix($operator);\n      $value: get-expression-value($condition, $operator);\n\n      @if ($prefix == 'max' and $value <= $no-media-breakpoint-value) or ($prefix == 'min' and $value > $no-media-breakpoint-value) {\n        @return false;\n      }\n    }\n\n    @else if not index($im-no-media-expressions, $condition) {\n      @return false;\n    }\n  }\n\n  @return true;\n}\n\n////\n/// Parsing engine\n/// @author Hugo Giraudel\n/// @access private\n////\n\n///\n/// Get operator of an expression\n///\n/// @param {String} $expression - Expression to extract operator from\n///\n/// @return {String} - Any of `>=`, `>`, `<=`, `<`, ``, ``\n///\n@function get-expression-operator($expression) {\n  @each $operator in ('>=', '>', '<=', '<', '', '') {\n    @if str-index($expression, $operator) {\n      @return $operator;\n    }\n  }\n\n  // It is not possible to include a mixin inside a function, so we have to\n  // rely on the `im-log(..)` function rather than the `log(..)` mixin. Because\n  // functions cannot be called anywhere in Sass, we need to hack the call in\n  // a dummy variable, such as `$_`. If anybody ever raise a scoping issue with\n  // Sass 3.3, change this line in `@if im-log(..) {}` instead.\n  $_: im-log('No operator found in `#{$expression}`.');\n}\n\n///\n/// Get dimension of an expression, based on a found operator\n///\n/// @param {String} $expression - Expression to extract dimension from\n/// @param {String} $operator - Operator from `$expression`\n///\n/// @return {String} - `width` or `height` (or potentially anything else)\n///\n@function get-expression-dimension($expression, $operator) {\n  $operator-index: str-index($expression, $operator);\n  $parsed-dimension: str-slice($expression, 0, $operator-index - 1);\n  $dimension: 'width';\n\n  @if str-length($parsed-dimension) > 0 {\n    $dimension: $parsed-dimension;\n  }\n\n  @return $dimension;\n}\n\n///\n/// Get dimension prefix based on an operator\n///\n/// @param {String} $operator - Operator\n///\n/// @return {String} - `min` or `max`\n///\n@function get-expression-prefix($operator) {\n  @return if(index(('<', '<=', ''), $operator), 'max', 'min');\n}\n\n///\n/// Get value of an expression, based on a found operator\n///\n/// @param {String} $expression - Expression to extract value from\n/// @param {String} $operator - Operator from `$expression`\n///\n/// @return {Number} - A numeric value\n///\n@function get-expression-value($expression, $operator) {\n  $operator-index: str-index($expression, $operator);\n  $value: str-slice($expression, $operator-index + str-length($operator));\n\n  @if map-has-key($breakpoints