   margin-top: $space;\n  }\n\n  > *:not(.o-section) {\n    max-width: $large;\n    margin-left: auto;\n    margin-right: auto;\n  }\n\n  > dl dd,\n  > dl dt,\n  > ol li,\n  > ul li,\n  > p {\n    @include p;\n  }\n\n  h2:empty,\n  h3:empty,\n  p:empty {\n    display: none;\n  }\n\n  > h1,\n  > h2,\n  > h3 {\n    padding-top: $space;\n  }\n\n  > h4 {\n    margin-bottom: -$space-half;\n  }\n\n  .wp-block-buttons.aligncenter {\n    .wp-block-button {\n      width: 100%;\n      margin-left: auto;\n      margin-right: auto;\n    }\n  }\n\n  .wp-block-button__link {\n    @include o-button;\n    @include o-button--primary;\n  }\n\n  hr {\n    margin-top: $space-double;\n    margin-bottom: $space-double;\n  }\n\n  hr.o-hr--small {\n    margin-top: $space;\n    margin-bottom: $space;\n  }\n\n  code,\n  pre {\n    font-size: 125%;\n  }\n}\n\nlabel,\n.o-form div.wpforms-container-full .wpforms-form .wpforms-field-label,\ndiv.wpforms-container-full .wpforms-form .wpforms-field-label {\n  @include o-heading--xs;\n}\n",".o-dek {\n  color: $c-gray;\n}\n",".o-rich-text {\n  width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n  pointer-events: auto;\n\n  & > * + * {\n    margin-top: $space;\n\n    @include media('>medium') {\n      margin-top: $space-and-half;\n    }\n  }\n\n  > h1,\n  > h2,\n  > h3,\n  > h4,\n  > h5,\n  > h6 {\n    padding-top: $space;\n    margin-bottom: -$space-half;\n  }\n\n  > *:first-child {\n    margin-top: 0;\n    padding-top: 0;\n  }\n\n  h2:empty,\n  h3:empty,\n  p:empty {\n    display: none;\n  }\n\n  > p a,\n  > a {\n    @include o-link;\n    display: unset;\n  }\n\n  > hr {\n    margin: $space-triple auto;\n  }\n\n  > ul {\n    margin-left: $space-double;\n  }\n}\n",".c-card {\n  background-color: white;\n  border-radius: $border-radius--large;\n  box-shadow: $box-shadow--standard;\n  padding: $space-double;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n}","/* ------------------------------------ *\\\n    $SPEC