 color: $c-black;\n    }\n  }\n}","/* ------------------------------------ *\\\n    $NAVIGATION\n\\* ------------------------------------ */\n\n/**\n * Drawer menu\n */\nbody.menu-is-active {\n  overflow: hidden;\n\n  &::before {\n    opacity: 1;\n    visibility: visible;\n    z-index: 9998;\n\n    @include media(\">xlarge\") {\n      opacity: 0;\n      visibility: hidden;\n    }\n  }\n\n  .c-nav-drawer {\n    right: 0;\n  }\n}\n\n.c-nav-drawer {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  width: 100vw;\n  height: 100vh;\n  background-color: $c-white;\n  position: fixed;\n  z-index: 9999;\n  top: 0;\n  right: -100vw;\n  transition: right 0.25s $transition-effect;\n\n  @include media(\">small\") {\n    width: 100%;\n    max-width: 400px;\n    right: -400px;\n  }\n\n  @include media(\">xlarge\") {\n    display: none;\n  }\n\n  &__toggle {\n    background-color: transparent;\n    justify-content: flex-start;\n    padding: $space;\n    outline: 0;\n    border: 0;\n    border-radius: 0;\n    background-image: none;\n\n    .o-icon {\n      transition: transform 0.25s $transition-effect;\n      transform: scale(1);\n    }\n\n    &:hover,\n    &:focus {\n      .o-icon {\n        transform: scale(1.1);\n      }\n    }\n  }\n\n  &__nav {\n    height: 100%;\n    padding-top: $space-double;\n  }\n\n  &__social {\n    border-top: $border--standard-light;\n\n    .c-social-links {\n      justify-content: space-evenly;\n\n      &__item {\n        border: 0;\n        border-radius: 0;\n        background: none;\n        margin: 0;\n\n        svg path {\n          fill: $c-gray--light;\n        }\n\n        &:hover,\n        &:focus {\n          svg path {\n            fill: $c-primary;\n          }\n        }\n      }\n    }\n  }\n}\n\n/**\n * Primary nav\n */\n.c-nav-primary {\n  &__menu-item {\n    margin: 0 $space-double;\n\n    @include media(\">xlarge\") {\n      margin: 0 $space;\n\n      &:last-child {\n        margin-right: 0;\n      }\n    }\n  }\n\n  &__list {\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n    justify-content: flex-start;\n\n    @include media(\">xlarge\") {\n      flex-direction: row;\n      align-items: center;\n      justify-content: flex-end;\n    }\n  }\n\n  &__menu-item:not(.button) a {\n    width: 100%;\n    padding: $space 0;\n    border-bottom: $border--standard-light;\n    color: $c-black;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n\n    @include o-heading--s;\n\n    @include media(\">xlarge\") {\n      width: 100%;\n      padding: $space-quarter 0;\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n      color: $c-black;\n      border-bottom: 1px solid transparent;\n      text-align: center;\n    }\n\n    &:hover,\n    &:focus {\n      color: $c-black;\n\n      @include media(\">xlarge\") {\n        border-bottom: 1px solid $c-black;\n      }\n\n\n      &::after {\n        opacity: 1;\n        visibility: visible;\n        left: 0;\n      }\n    }\n\n    &::after {\n      opacity: 0;\n      visibility: hidden;\n      content: \"\";\n      color: $c-gray--light;\n      font-size: 22px;\n      line-height: 1;\n      transition: $transition-all;\n      position: relative;\n      left: -10px;\n      transition-delay: 0.25s;\n\n      @include media(\">xlarge\") {\n        display: none;\n      }\n    }\n  }\n\n  &__menu-item.button a {\n    @include o-button;\n    @include o-button--secondary;\n\n    @include media(\"<=xlarge\") {\n      margin-top: $space;\n      width: 100%;\n    }\n\n\n    &::after {\n      display: none;\n    }\n  }\n}\n\n/**\n * Utility nav\n */\n.c-nav-utility {\n  display: flex;\n  flex-direction: column;\n  align-items: stretch;\n  justify-content: stretch;\n  margin: $space-double;\n\n  @include media(\">medium\") {\n    flex-direction: row;\n    align-items: center;\n    justify-content: flex-end;\n    margin: 0 (-$space-half);\n  }\n\n\n  &__link {\n    @include o-heading--xs;\n\n    color: $c-primary;\n    padding: $space-half 0;\n    position: relative;\n\n    @include media(\">medium\") {\n      color: $c-white;\n      padding: 0 $space-half;\n      height: 100%;\n      line-height: 40px;\n    }\n\n\n    &:hover,\n    &:focus {\n      color: $c-black;\n\n      @include media(\">medium\") {\n        color: $c-white;\n\n        &::after {\n          background-color: $c-secondary;\n        }\n      }\n    }\n\n    &::after {\n      content: \"\";\n      display: block;\n      width: 100%;\n      height: 100%;\n      background-color: transparent;\n      position: absolute;\n      top: 0;\n      left: 0;\n      z-index: -1;\n      transform: skewX(-20deg);\n      transition: $transition-all;\n      pointer-events: none;\n    }\n  }\n}\n\n/**\n * Footer nav\n */\n.c-nav-footer {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  margin-bottom: -$space-half;\n\n  &__link {\n    color: $c-white;\n    padding: $space-half;\n    border-radius: 50px;\n\n    @include o-heading--xs;\n\n    &:hover,\n    &:focus {\n      color: $c-white;\n      background-color: $c-primary;\n    }\n  }\n}\n\n/**\n * Footer legal nav\n */\n.c-nav-footer-legal {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  margin-left: -$space-half;\n  margin-right: -$space-half;\n\n  @include media(\">medium\") {\n    justify-content: flex-end;\n  }\n\n\n  &__link {\n    color: $c-white;\n    padding: $space-quarter $space-half;\n    text-decoration: underline;\n\n    &:hover,\n    &:focus {\n      color: $c-white;\n    }\n  }\n}\n\n/**\n * Dashboard nav\n */\n.c-dashboard-nav {\n  position: sticky;\n  top: $space-double;\n  left: $space-half;\n\n  @include media('>medium') {\n    left: $space;\n  }\n\n  &::before {\n    content: \"\";\n    display: block;\n    width: 1px;\n    height: calc(100% - #{$icon-large});\n    position: absolute;\n    left: calc(#{$icon-large} /2);\n    top: 0;\n    background-color: $c-gray--light;\n    z-index: -1;\n  }\n\n  &__parent-list {\n    overflow: hidden;\n  }\n\n  &__link {\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    color: $c-gray--light;\n    margin-bottom: $space;\n    white-space: nowrap;\n\n    span + span {\n      padding-left: $space-half;\n    }\n\n    .o-icon {\n      transition: background-color 0.25s ease;\n    }\n\n    &:hover,\n    &:focus {\n      color: $c-black;\n\n      .o-icon {\n        background-color: $c-secondary;\n\n        svg path {\n          fill: white;\n        }\n      }\n    }\n\n    &.is-active {\n      color: $c-black;\n\n      .o-icon {\n        background-color: $c-primary;\n        border-color: $c-primary;\n\n        svg path {\n          fill: white;\n        }\n      }\n    }\n  }\n\n  &__icon {\n    height: $icon-large;\n    width: $icon-large;\n    min-width: $icon-large;\n    border-radius: $icon-large;\n    background-color: white;\n    border: $border--standard;\n\n    svg path {\n      fill: $c-secondary;\n    }\n  }\n}\n\n/**\n * Dashboard support nav\n */\n.c-dashboard-support-nav {\n  transition: all 0.125s ease;\n  opacity: 0;\n  visibility: hidden;\n  margin-top: $space-double;\n  padding-bottom: 80px;\n  position: sticky;\n  bottom: 0;\n  width: 100%;\n\n  @include media('>medium') {\n    opacity: 1;\n    visibility: visible;\n    padding-bottom: $space;\n  }\n\n  &::before {\n    display: block;\n    content: \"Support\";\n    @include o-heading--s;\n    color: $c-gray--light;\n    padding-bottom: $space-half;\n    margin-bottom: $space-half;\n    border-bottom: $border--standard;\n    width: 100%;\n  }\n\n  a {\n    color: $c-black;\n    border: 0;\n    margin-bottom: $space-quarter;\n\n    &:hover,\n    &:focus {\n      color: $c-primary;\n    }\n  }\n}\n",".c-dashboard-sidebar {\n  position: relative;\n\n  &__container {\n    position: relative;\n    heig