","/* ------------------------------------ *\\\n    $HEADER\n\\* ------------------------------------ */\n\n#wpadminbar {\n  display: none;\n}\n\nhtml {\n  margin-top: 0 !important;\n}\n\n.logged-in .c-utility {\n  top: 0;\n}\n\n.c-utility {\n  position: relative;\n  top: 0;\n  z-index: 9;\n  height: 40px;\n  background: $c-primary;\n\n  @include media(\">medium\") {\n    position: sticky;\n  }\n\n\n  &--inner {\n    display: flex;\n    align-items: stretch;\n    justify-content: space-between;\n  }\n\n  &__nav {\n    @include media(\"<=medium\") {\n      display: none;\n    }\n  }\n\n  &__social {\n    position: relative;\n    left: -$space-half;\n\n    a {\n      border: 0;\n      border-radius: 0;\n      background: none;\n      margin: 0;\n      height: 40px;\n\n      svg path {\n        fill: $c-white;\n      }\n\n      &:hover,\n      &:focus {\n        background-color: $c-secondary;\n\n        svg path {\n          fill: $c-white;\n        }\n      }\n    }\n  }\n}\n\n.c-header {\n  border-bottom: $border--standard-light;\n  background-color: $c-white;\n\n  &--inner {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n  }\n\n  &__logo {\n    display: flex;\n    align-items: center;\n    max-width: 240px;\n    padding: $space 0;\n\n    img {\n      width: 100%;\n    }\n  }\n\n  &__nav {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    .c-nav-primary {\n      display: none;\n\n      @include media(\">xlarge\") {\n        display: flex;\n      }\n    }\n\n    .o-toggle {\n      border-radius: 0;\n      background: none;\n      border: 0;\n      position: relative;\n      right: -$space;\n      padding: $space;\n\n      @include media(\">xlarge\") {\n        display: none;\n      }\n    }\n  }\n}\n\n/* ------------------------------------ *\\\n    $FOOTER\n\\* ------------------------------------ */\n\n.c-footer {\n  position: relative;\n  z-index: 1;\n  background-color: $c-secondary;\n  font-we