\n      left: 50%;\n      top: 0;\n      z-index: 0;\n\n      @include media(\">large\") {\n        display: none;\n      }\n    }\n\n    &::after {\n      content: \"\";\n      display: block;\n      height: 100%;\n      width: 100%;\n      position: absolute;\n      z-index: 0;\n      border-left: 50vw solid transparent;\n      border-right: 50vw solid transparent;\n      border-top: 60vw solid $c-primary;\n      transform: translate(-50%, 0);\n      left: 50%;\n      top: 50%;\n\n      @include media(\">large\") {\n        border-top: 400px solid transparent;\n        border-bottom: 400px solid transparent;\n        border-right: 460px solid $c-primary;\n        border-left: none;\n        transform: translate(0, -50%);\n        left: -50%;\n        top: 50%;\n      }\n    }\n  }\n}\n\n/**\n * Cards\n */\n.c-section-cards {\n  &__buttons {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    a {\n      margin: 0 $space-half;\n    }\n\n    a:last-child {\n      @include o-button--tertiary;\n    }\n  }\n\n  .c-cards {\n    grid-column-gap: 0;\n\n    @include media(\">medium\") {\n      grid-column-gap: $space-double;\n    }\n  }\n\n  .c-card {\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n    background-color: $c-white;\n    border-radius: $border-radius--large;\n    padding: $space;\n    text-align: center;\n\n    picture {\n      display: block;\n      overflow: hidden;\n      border-radius: 50%;\n    }\n\n    &__description {\n      flex: 1;\n    }\n  }\n}\n\n/**\n * Image\n */\n// .c-section-image {\n//   background-color: $c-secondary;\n//   min-height: 60vh;\n//   position: relative;\n//   display: flex;\n//   flex-direction: column;\n//   align-items: center;\n//   justify-content: center;\n//\n//   &--inner {\n//     display: flex;\n//     flex-direction: column;\n//     align-items: center;\n//     justify-content: center;\n//     text-align: center;\n//     color: $c-white;\n//     position: rela